import Vue from 'vue'
import Vuex from 'vuex'
// import createPersistedState from 'vuex-persistedstate';

Vue.use(Vuex)
var act = {
  'setUser': 'SET_USER',
  'addBets': 'ADD_BETS'
}

export default new Vuex.Store({
  state: {
    user: {},
    bets: []
  },
  mutations: {
    [act.setUser]: (state, user) => {
      state.user = user;
    },
    [act.addBets]: (state, bets) => {
      state.bets.unshift(...bets);
    }
  },
  actions: {
    setUser: ({commit}, user) => {
      commit(act.setUser, user);
    },
    addBets: ({commit}, bets) => {
      Array.isArray(bets) ? commit(act.addBets, bets) : commit(act.addBets, [bets]);
    }
  },
  modules: {},
  // plugins: [createPersistedState({key: 'SADBOIS'})]
})
