<template>
  <Panel>
    <div slot="title" class="text-h5 font-weight-black text-center">{{ user.name }}</div>
    <v-container class="text-center">
      <div>Todos tus puntos: {{user.totalPoints}}</div>
      <div>Puntos disponibles: {{user.points}}</div>
      <div>Puntos en uso: {{user.usedPoints}}</div>
    </v-container>
    <v-container>
      <v-data-table :headers="table.headers" :items="bets">
        <template v-slot:[`item.createdAt`]="{value}">{{yay(value)}}</template>
        <template v-slot:[`item.betDay`]="{value}">{{yay(value)}}</template>
      </v-data-table>
    </v-container>
  </Panel>
</template>
<script>
import Panel from "@/components/Base/Panel.vue";
export default {
  data: function(){
    return {
      table: {
        headers: [
          {text: "Puntos", value: "points"},
          {text: "Compra", value: "compra"},
          {text: "Venta", value: "venta"},
          {text: "Dia apostado:", value: "betDay"},
          {text: "Creado el:", value: "createdAt"},
        ]
      }
    }
  },
  methods: {
    yay: function(item){
      const d = new Date(item);
      return d.toLocaleDateString();
    }
  },
  components: {
    Panel,
  },
  computed: {
    user: function () {
      return this.$store.state.user;
    },
    bets: function(){
      return this.$store.state.bets;
    }
  },
};
</script>

