<template>
  <Panel>
    <div slot="title" class="text-h5 font-weight-black text-center">Tabla de lideres!</div>
    <v-container name="boardList">
      <div
        :class="`d-flex pr-8 pl-8 _board_c_${index}`"
        v-for="(item, index) in boardArray"
        :key="item._id"
      >
        <div class="">{{ yep(index) }}</div>
        <div class="ml-5">{{ item.name }}</div>
        <div class="ml-auto mr-2">
          {{ item.totalPoints }}
        </div>
      </div>
      <div v-if="boardArray.length == 0">Esta tabla se va a actualizar todos los dias a las 8PM. Si ves esto, puede que sea un error, o que sea el primer dia del servidor activo. Suerte!</div>
    </v-container>
  </Panel>
</template>
<script>
import Panel from "@/components/Base/Panel.vue";
export default {
  data: function () {
    return {
      boardArray: [],
    };
  },
  components: { Panel },
  mounted() {
    this.updateBoard();
  },
  methods: {
    updateBoard: function () {
      fetch("/api/get_board")
        .then((resp) => resp.json())
        .then((json) => {
          this.boardArray = JSON.parse(json.board);
        });
    },
    yep: index => ++index,
  },
};
</script>
<style>
._board_c_0 {
  font-size: 22pt;
}
._board_c_1 {
  font-size: 18pt;
}
._board_c_2 {
  font-size: 16pt;
}
</style>
