<template>
  <v-app>
    <router-view />
  </v-app>
</template>
<script>
export default {
  name: "App",
  created: function () {
    fetch("/api/account")
      .then((resp) => resp.json())
      .then((json) => {
        if (Object.prototype.hasOwnProperty.call(json, "user")) {
          this.$store.dispatch("setUser", json.user);
          this.$store.dispatch("addBets", json.bets);
        }
      });
  },
};
</script>
