<template>
  <div>
    <v-app-bar
      app
      color="primary"
      dark
    >
      <div class="title">DolarSi DolarNo</div>
      <v-spacer></v-spacer>
      <div v-text="user.points" v-if="user.name"></div>
      <v-btn
        @click="logOut"
        target="_blank"
        text
        v-if="user.hasOwnProperty('name')"
      >
        <span class="mr-2">LogOut</span>
        <v-icon>mdi-open-in-new</v-icon>
      </v-btn>
      <v-btn
        @click="openLogIn"
        target="_blank"
        text
        v-if="!user.hasOwnProperty('name')"
      >
        <span class="mr-2">LogIN</span>
        <v-icon>mdi-open-in-new</v-icon>
      </v-btn>
    </v-app-bar>
    <v-main>
      <slot></slot>
    </v-main>
  </div>
</template>

<script>
export default {
  name: 'AppLayout',
  components: {
  },
  data: () => ({}),
  computed: {
    user(){
      return this.$store.state.user;
    }
  },
  methods: {
    openLogIn: function () {
      window.location.href = '/api/auth/reddit';
    },
    logOut: function () {
      window.location.href = '/api/logout';
    }
  }
};
</script>
