<template>
  <Panel>
    <div slot="title" class="text-h5 font-weight-black text-center">
      DOLARCITO
    </div>
    <v-container>
      <v-text-field
        label="Compra"
        type="number"
        v-model="compra"
      ></v-text-field>
      <v-text-field label="Venta" type="number" v-model="venta"></v-text-field>
      <v-text-field
        label="Puntos a apostar"
        type="number"
        v-model="pointsToBet"
      ></v-text-field>
      <div class="d-flex" v-if="false">
        <v-date-picker v-model="picker" :landscape="true" class="ma-auto"></v-date-picker>
      </div>
      <div class="d-flex mt-2">
        <v-btn @click="this.shout">Enviar!</v-btn>
        <div class="flex-grow-1" :style="styleData">{{ messageData }}</div>
      </div>
    </v-container>
  </Panel>
</template>
<script>
import Panel from "@/components/Base/Panel.vue";
export default {
  name: "BetComp",
  components: {
    Panel,
  },
  data: function () {
    return {
      picker: "",
      compra: "",
      venta: "",
      pointsToBet: "",
      styleData: "",
      messageData: "",
    };
  },
  methods: {
    shout: function () {
      var user = this.$store.state.user;
      if (!Object.prototype.hasOwnProperty.call(user, 'name')) {
        this.styleData = "background-color: purple;";
        this.messageData = "Tenes que loguear primero! :D";
        setTimeout(() => {
          this.styleData = "";
          this.messageData = "";
        }, 3500);
        return null;
      }
      fetch("/api/create_bet", {
        headers: {
          "Content-Type": "application/json",
        },
        method: "post",
        body: JSON.stringify({
          compra: this.compra,
          venta: this.venta,
          pointsToBet: this.pointsToBet,
          betDay: this.picker
        }),
      })
        .then((response) => response.json())
        .then((json) => {
          this.compra = "";
          this.venta = "";
          this.pointsToBet = "";
          if (json.user) {
            this.$store.dispatch("setUser", {
              ...this.$store.state.user,
              ...json.user,
            });
            this.$store.dispatch('addBets', json.bet);
            this.styleData = "background-color: green";
            this.messageData = "Apuesta enviada. Suerte!";
          } else if (json.error) {
            this.styleData = "background-color: red";
            this.messageData = json.error;
          }
          setTimeout(() => {
            this.styleData = "";
            this.messageData = "";
          }, 3500);
        });
    },
  },
};
</script>
